*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html{
  font-family: "Montserrat",sans-serif;
}
body{
  background: #eee;
  padding: 0 16px;
}
header{
  background-color: #f5ba13;
  margin: auto -16px;
  padding:16px 32px;
  box-shadow: 0 0 10px 5px rgba(0,0,0,0.3);
}
header h1{
  color:#fff;
  font-family: "McLaren", cursive;
  font-weight: 200;

}
footer{
  position: absolute;
  text-align: center;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
}
footer p{
  color:grey;
}
.note{
  background: #fff;
  border-radius: 7px;
  box-shadow: 0 0 2px 5px white;
  padding: 10px;
  width: 240px;
  margin: 16px;
  float: left;
}
.txta{
  background: #fff;
  
  box-shadow: 0 2px 5px #ccc;
  padding: 10px;
 
  margin: 16px;
border: none;
margin-left: auto;
    margin-right: auto;
    display: block;
    position: relative;
    top:-16px;
    width:500px;
    border-bottom-left-radius: 7px;
  border-bottom-right-radius:7px;
}
.inp{
  background: #fff;
  border-top-left-radius: 7px;
  border-top-right-radius:7px;
  box-shadow: 0 2px 5px #ccc;
  padding: 10px;
  width:500px;
  margin: 16px;
  border:none;
  margin-left: auto;
  margin-right: auto;
  display: block;
}
.btnadd{
  background-color: #f5ba13;
  color:#fff;
  font-family: "McLaren", cursive;
  font-weight: 200;
  border:none;
  border-radius: 100%;
  width:30px;
  height: 30px;
  position: relative;
  left:950px;
  top:-47px;
}
@media only screen and (max-width: 1200px) {
 
  .btnadd{
    position: relative;
  left:360px;
  top:-47px;
  }}

  @media only screen and (max-width: 1500px) {
   
    .btnadd{
      position: relative;
    left:200px;
    top:-47px;
    }
  }
    @media only screen and (max-width: 1200px) {
     
      .btnadd{
        position: relative;
      left:460px;
      top:-47px;
      }
}
.note h1{
  font-size: 1.1em;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;

}
.note h1{
  font-size:1.1rem;
  margin-bottom:6px;
}